* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #e0e8ff;
}

div#quote {
  font-size: 120%;
  margin: 1em;
  font-family: cursive;
  padding-top: 1em;
}

.footer {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #aa93df4d;
  width: 90vw;
  margin: 0 auto;
  max-width: 500px;
  padding: 2em;
}

main {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  width: 90vw;
  max-width: 500px;
  margin: 0 auto;
  margin-top: 50px;
  padding-bottom: 20px;
}

h1 {
  background-color: #1b1096;
  color: white;
  width: 100%;
  margin-bottom: 10px;
  font-size: 2rem;
  padding: 20px 10px;
}

.output {
  padding: 10px 5px;
  border-radius: 9999px;
  background-color: #1b1096;
  color: white;
  margin-top: 10px;
}

.output-section {
  margin-top: 20px;
}

.input-section {
  width: 70%;
}

.input-slider {
  width: 100%;
  margin-top: 5px;
}

.slider-output {
  text-align: left;
  margin-top: 10px;
}
